.bullet-card {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	font-size: 1rem;
	padding: 3em 1.4em 3.3em 2.1em;
	background: var(--bg-card-dark);
	border-radius: 2em;
}

.bullet-card__media {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 6.6em;
	max-width: 6.6em;
	min-width: 6.6em;
	height: 7.3em;
	max-height: 7.3em;
	min-height: 7.3em;
	border-radius: 2em;
	border: 0.1em solid rgba(255, 255, 255, 0.10);
	background: linear-gradient(164deg, #6AC0FF 1.46%, #086FFF 102.79%);
	margin-right: 1.2em;
}

.bullet-card__pic {
	display: block;
	width: 30%;
	height: 30%;
}

.bullet-card__img {
	display: block;
	width: 100%;
	height: 100%;
}

.bullet-card__content {
	--fg: var(--fg-on);
	--fz-h3: 2.4em;
	--fz-p: 1.5em;
	--margin-top-p: 0.6em;
}

.bullet-card__content p {
	padding-right: 2em;
}